<template>
  <ul>
    <template v-for="index in 5">
      <li @click="liClickFn"
          class="menuLi"
          :key="index"
          :ref="'liMenu'+index">菜单{{index}}</li>
    </template>
  </ul>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    liClickFn() {
      const elementArr = this.$refs;
      for (let elementObj in elementArr) {
        elementArr[elementObj][0].classList.remove("isActive");
      }
      event.srcElement.classList.add("isActive");
    }
  }
};
</script>
<style socped>
.isActive {
  background: #99a9bf !important;
}
.menuLi {
  list-style: none;
  height: 48px;
  line-height: 48px;
  padding: 0 20px;
  background: #475669;
  cursor: pointer;
  border-radius: 3px;
  float: left;
  margin: 5px;
}
.menuLi:hover {
  background: #42b983;
}
</style>



